<template>
    <div class="myPasslogin">
         <van-nav-bar
            :border='false'
            >
            <template #left>
                 <span  class="text"  >
                     <span class="iconfont icon-arrow-fine-up-copy" @click="onClickLeft"></span> 
                     手机号登录
                </span>
            </template>
            </van-nav-bar>

            <van-field v-model="password" :type="isEye ? 'text' : 'password'"  placeholder="请输入密码" >
                <template #button>
                    <van-icon @click="ff_Eye" :name=" isEye ? 'eye-o' : 'closed-eye'" />
                </template>
            </van-field>
            
            <van-button class="nexts" round block type="info" native-type="submit" :color="isColor ? '#fe3a3b' :  '#ff7f7e'" @click="ff_tast" @keyup.Enter='ff_tast'>
                登录
            </van-button>
           
            <p>手机号已与帐号{{useObj.nickname}}绑定</p>
    </div>
</template>

<script>
   import {inspectFrom} from '../assets/js/inspectFrom/inspectFrom.js'
    export default {
        name : 'Passlogin',
        data() {
            return {
                password  : '',
                useObj    : {},
                isColor   : false,
                isEye     : false,
             
            }
        },
        created(){
            this.useObj = this.$route.query;
            console.log(this.$route.query)
        },
        methods:{
            // 睁闭眼
            ff_Eye(){
                  this.isEye = !this.isEye;
            },
            onClickLeft(){
                this.$router.go(-1);
                
            },
            ff_clears(){
                this.tel = ''
            },
            ff_tast(){
                 let obj = {
                    password:{
                        value    :  this.password,
                        errorMsg :  '密码格式错误',
                        reg      :   /^[A-Za-z]\w.{5,15}$/
                    },
                }

                 let isTast = inspectFrom.inspect(obj);
                 console.log(isTast)

                 if(isTast){
                   
                    this.axios({
                        url:"/login/cellphone",
                        method:'GET',
                        params:{
                            phone    : this.useObj.phone,
                            password : this.password
                        }
                    })
                    .then(res=>{
                        console.log(res)
                        if(res.data.code == 200){
                              window.localStorage.setItem('useProfile',JSON.stringify(res.data.profile))
                              window.localStorage.setItem('useId',res.data.token);
                              this.$router.push({name : 'Find'})

                        }else{
                            this.$toast(res.data.msg)
                        }
                      
                        
                    })
                    .catch(err=>{
                        console.log(err)
                    })
                }
              
            },
        },
        watch: {
            password(val){
                if(val.length>=5){
                     this.isColor = true;
                }else{
                     this.isColor = false;
                }
            },
            useObj(val , old){
                  console.log(val,old)
            }
        },
    }
</script>

<style lang="less" scoped>
     .myPasslogin{

         /deep/.van-nav-bar__text{
             color: #000;
             font-size: 16px;
             font-weight: 550;
               
         }
         /deep/.van-nav-bar__content{
             height: 75px;
         }
        .text{
                font-size: 0.45333rem;
                font-weight: 800;
                -webkit-transform: scale(0.9);
                font-size: 22px;
                transform: scale(0.9);
                margin-left: 0.26667rem;
                display: inline-block;
                font-family: '宋体';


                .iconfont{
                    font-size: 20px;
                    margin-left: -18px;
                }
             }
        >p{
            text-align: left;
            font-size: 13px;
            color: #979797;
            box-sizing: border-box;
            padding: 0px 15px;

        }
        /deep/.van-field__label{
                color: #646566;
                text-align: left;
                font-size: 21px;
                font-weight: 548;
        }
        /deep/.van-field__label{
            width: 40px;
        }
        /deep/.van-cell{
            padding: 10px 0px;
            width: 92%;
            margin: 0px auto;
            border-bottom: 1px solid #e7e7e7;
            box-sizing: border-box;
            position: relative;
            margin-top: 23px;
        }
        /deep/.van-icon{
            font-size: 20px;
            position: absolute;
            right: 3px;
            top: 2px;

        }
        .nexts{
            width: 93%;
            height: 45px;
            line-height: 45px;
            font-weight: 500;
            font-size: 18px;
            text-align: center;
            color: #fff;
        
            border-radius: 25px;
            margin: 55px auto 20px;
            letter-spacing: 3px;
        }
        .forge{
            font-size: 13px;
            color: #5f80a4;
        }
        p{
            text-align: center;
            font-size: 12px;
            color: #a3a3a3;
        }

     }

</style>